<template>
    <div class="flex flex-col bg-black/40 w-full h-full max-h-screen">
        <div class="flex flex-col mb-4 pt-4 px-2">
            <div class="flex flex-row text-center">
                <div :class="[selectedTab === 'chat' ? 'text-white' : 'text-gray-500', 'grow', 'cursor-pointer', 'flex', 'justify-center']"
                    @click="selectedTab = 'chat'">
                    <div class="relative">
                        Chat
                        <div v-if="chatMarked" class="absolute w-2 h-2 rounded-full bg-red-600 top-0 -right-3"></div>
                    </div>
                </div>
                <div :class="[selectedTab === 'preguntas' ? 'text-white' : 'text-gray-500', 'grow', 'cursor-pointer', 'flex', 'justify-center']"
                    @click="selectedTab = 'preguntas'">
                    <div class="relative">
                        Preguntas
                        <div v-if="preguntasMarked" class="absolute w-2 h-2 rounded-full bg-red-600 top-0 -right-3">
                        </div>
                    </div>
                </div>
                <div :class="[selectedTab === 'encuestas' ? 'text-white' : 'text-gray-500', 'grow', 'cursor-pointer', 'flex', 'justify-center']"
                    @click="selectedTab = 'encuestas'">
                    <div class="relative">
                        Encuestas
                        <div v-if="encuestasMarked" class="absolute w-2 h-2 rounded-full bg-red-600 top-0 -right-3">
                        </div>
                    </div>
                </div>
                <div :class="[selectedTab === 'agenda' ? 'text-white' : 'text-gray-500', 'grow', 'cursor-pointer', 'flex', 'justify-center']"
                    @click="selectedTab = 'agenda'">
                    <div class="relative">
                        Agenda
                        <div v-if="agendaMarked" class="absolute w-2 h-2 rounded-full bg-red-600 top-0 -right-3"></div>
                    </div>
                </div>

                <div v-if="store.state.mode === 'presential'" :class="[selectedTab === 'agenda' ? 'text-white' : 'text-gray-500', 'grow', 'cursor-pointer', 'flex', 'justify-center']"
                    @click="selectedTab = 'qr'">
                    <div class="relative">
                        QR
                    </div>
                </div>
            </div>
        </div>

        <div v-if="selectedTab === 'chat'" class="flex basis-full grow-0 overflow-y-hidden">
            <div class="flex flex-col gap-4 basis-full  grow-0">
                <div class="flex flex-row px-9 text-sm mb-2">
                    <p class="underline decoration-2 underline-offset-8">Public</p>
                </div>

                <div id="messages" class="flex flex-col flex-nowrap basis-full grow-0 pl-2 pr-4 gap-4 overflow-y-auto">
                    <div v-for="(message, index) of messages" class="flex flex-row gap-2" :key="index">
                        <template v-if="message.type === 'message'">
                            <div class="flex rounded-full text-xl justify-center items-center"
                                :style="{ backgroundColor: message.name.toHex() }"
                                style="min-width: 40px; height: 40px;">
                                {{ getInitials(message.name) }}
                            </div>

                            <div class="rounded-b rounded-tr bg-white w-full p-2 text-black flex flex-col">
                                <p class="text-slate-500 text-xs">{{ message.name }}</p>
                                <p class="text-sm">{{ message.text }}</p>
                            </div>
                        </template>

                        <div v-else-if="message.type === 'question'"
                            class="rounded bg-white p-2 text-sky-800 border-2 border-sky-800 text-center cursor-pointer" @click="selectedTab = 'preguntas'">
                            <p>El anfitrión esta aceptando preguntas. Pincha aquí para participar.</p>
                        </div>

                         <div v-else-if="message.type === 'poll'"
                            class="rounded bg-white p-2 text-sky-800 border-2 border-sky-800 text-center cursor-pointer" @click="selectedTab = 'encuestas'">
                            <p>El anfitrión ha enviado una pregunta. Pincha aquí para participar.</p>
                        </div>
                    </div>
                </div>

                <div class="flex flex-row">
                    <input type="text"
                        class="bg-black/40 text-sm disabled:border-gray-600 disabled:bg-slate-600 block w-full p-2.5"
                        placeholder="Escriba un mensaje..." v-model="newMessage" @keyup.enter="sendGeneralMessage">

                    <button class="px-4 bg-sky-500 text-white" type="button" @click="sendGeneralMessage"><ChatAltIcon class="h-5 w-5"/></button>
                </div>
            </div>
        </div>

        <div v-if="selectedTab === 'preguntas'"  class="flex basis-full grow-0 overflow-y-hidden">
            <Questions />
        </div>

        <div v-if="selectedTab === 'encuestas'"  class="flex basis-full grow-0 overflow-y-hidden">
            <Polls />
        </div>

        <div v-if="selectedTab === 'agenda'" class="flex basis-full grow-0 overflow-y-hidden">
            <Calendar />
        </div>

        <div v-if="selectedTab === 'qr'"  class="flex basis-full grow-0 overflow-y-hidden">
            <QR />
        </div>
    </div>
</template>

<script setup>
import { useAuth } from '@websanova/vue-auth';
import { computed, inject, nextTick, ref, watch } from 'vue'
import { useStore } from 'vuex'
import { ChatAltIcon } from '@heroicons/vue/outline'
import Calendar from './Calendar.vue'
import Questions from './Questions.vue'
import Polls from './Polls.vue'
import QR from './QR.vue'

const auth = useAuth()
const store = useStore()
const axios = inject('axios')
const selectedTab = ref('chat')
const newMessage = ref('')

const chatMarked = ref(false)
const preguntasMarked = ref(false)
const encuestasMarked = ref(false)
const agendaMarked = ref(false)

const messages = computed(() => store.state.messageList)
const questionRequests = computed(() => store.state.questionRequests)
const pollQuestions = computed(() => store.state.pollQuestions)

const messageDiv = ref(null)
// const interval = null
const messageScrollHeight = ref(0)
const messageScrollTop = ref(0)

nextTick(() => {            
    messageDiv.value = document.getElementById("messages")
    scrollToBottom()
    // interval = setInterval(() => {
    //     messageScrollHeight.value = messageDiv.value.scrollHeight
    //     messageScrollTop.value = messageDiv.value.scrollTop
    // }, 100);    
})

const scrollToBottom = () => {
    nextTick(() => {            
        messageDiv.value.scrollTop = messageDiv.value.scrollHeight
    })
}

watch(messages, () => {
    if (selectedTab.value !== 'chat') {
        chatMarked.value = true
    }

    scrollToBottom()
}, { deep: true })

watch(questionRequests, () => {
    if (selectedTab.value !== 'preguntas') {
        preguntasMarked.value = true
    }

    if (selectedTab.value !== 'chat') {
        chatMarked.value = true
        scrollToBottom()
    }
    //  else {
    //     nextTick(() => {
    //         const messageDiv = document.getElementById("messages")
    //         messageDiv.scrollTop = messageDiv.scrollHeight
    //     })
    // }
}, { deep: true })

watch(pollQuestions, () => {
    if (selectedTab.value !== 'encuestas') {
        encuestasMarked.value = true
    }

    if (selectedTab.value !== 'chat') {
        chatMarked.value = true
        scrollToBottom()
    }
    //  else {
    //     nextTick(() => {
    //         const messageDiv = document.getElementById("messages")
    //         messageDiv.scrollTop = messageDiv.scrollHeight
    //     })
    // }
}, { deep: true })

watch(questionRequests, () => {
    if (selectedTab.value !== 'preguntas') {
        preguntasMarked.value = true
    }

    if (selectedTab.value !== 'chat') {
        chatMarked.value = true
        scrollToBottom()
    }
    //  else {
    //     nextTick(() => {
    //         const messageDiv = document.getElementById("messages")
    //         messageDiv.scrollTop = messageDiv.scrollHeight
    //     })
    // }
}, { deep: true })

watch(selectedTab, (value) => {
    switch (value) {
        case "chat":
            chatMarked.value = false
            break

        case "preguntas":
            preguntasMarked.value = false
            break

        case "encuestas":
            encuestasMarked.value = false
            break

        case "agenda":
            agendaMarked.value = false
            break
    }
})

const sendGeneralMessage = () => {
    // called when the user sends a message
    if (!newMessage.value) return

    const message = {
        type: "message",
        text: newMessage.value.trim(),
        name: fullName(auth.user()),
        uuid: ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, (c) =>
            (
                c ^
                (crypto.getRandomValues(new Uint8Array(1))[0] & (15 >> (c / 4)))
            ).toString(16)
        ),
        // participant_id: auth.user().id
    }

    axios.post(
        "streaming/participations/" + auth.user().id + "/message",
        {
            activity_id: store.state.activity_id,
            message: JSON.stringify(message),
        }
    )

    newMessage.value = ""
    message.color = fullName(auth.user()).toHex()
    message.createdAt = Date.now()
    message.show = true
    store.commit('addToMessageList', message)
    scrollToBottom()
}

const fullName = (user) => {
    if (user.participant) {
        return user.participant.data.name + " " + user.participant.data.lastname
    } else {
        return user.name + " " + user.lastname
    }
}

const getInitials = (string) => {
    var names = string.split(' '),
        initials = names[0].substring(0, 1).toUpperCase()

    if (names.length > 1) {
        initials += names[names.length - 1].substring(0, 1).toUpperCase()
    }
    return initials
}
</script>

<style lang="scss" scoped>
</style>