<template>
  <div class="flex basis-full grow-0 overflow-y-hidden">
    <div id="messages" class="flex flex-col flex-nowrap basis-full grow-0 pl-2 pr-4 gap-4 overflow-y-auto">
      <div v-for="(request, index) of requests"
        class="flex flex-col gap-2 bg-white rounded text-black p-2 border-2 border-sky-800" :key="index">

        <template v-if="!request.done">
          <div>{{ request.message }}</div>

          <div class="flex rounded-full text-xl justify-center items-center">
            <textarea v-model="question" rows="4"
              class="block p-2.5 w-full text-sm text-gray-900 bg-gray-300 rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500"
              placeholder="Tu pregunta..."></textarea>
          </div>

          <button type="submit" class="btn uppercase" @click="sendQuestion(request)">Enviar</button>
        </template>

        <div v-else class="text-center text-sky-800">
          Gracias por participar!
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { useAuth } from '@websanova/vue-auth'
import { computed, inject, ref } from 'vue'
import { useStore } from 'vuex'
const auth = useAuth()
const store = useStore()
const axios = inject('axios')
const requests = computed(() => store.state.questionRequests)
const question = ref(null)

const sendQuestion = async (request) => {
  store.commit('questionDone', request.id)
  try {
    await axios.post(
      "streaming/participations/" + auth.user().id + "/sendQuestion",
      {
        activity_id: store.state.activity_id,
        question_request_id: request.id,
        question: question.value,
      }
    )
  } catch (e) {
    console.error(e)
  }
}
</script>

