<template>
  <div class="bg-white rounded-b-lg">
    <template v-if="store.state.sponsors.length > 0">
      <Carousel
        :autoplay="store.state.sponsors.length > 4 ? 5000 : 0"
        :itemsToShow="Math.min(store.state.sponsors.length, 4)"
        snapAlign="center"
        :wrapAround="true"
        :breakpoints="breakpoints"
        style="height: 100%"
      >
        <Slide v-for="sponsor of store.state.sponsors" :key="sponsor.id">
          <img
            :src="`${store.state.imagesURL}/${sponsor.logo}`"
            style="height: 100%"
          />
        </Slide>
      </Carousel>
    </template>
  </div>
</template>

<script setup>
import { Carousel, Slide } from "vue3-carousel";
import { useStore } from "vuex";
const store = useStore();

const breakpoints = {
  0: {
    itemsToShow: 1.5,
    snapAlign: store.state.sponsors <= 1 ? "center" : "start",
  },

  640: {
    itemsToShow: 2.5,
    snapAlign: store.state.sponsors <= 2 ? "center" : "start",
  },

  768: {
    itemsToShow: 3.5,
    snapAlign: store.state.sponsors <= 3 ? "center" : "start",
  },

  1024: {
    itemsToShow: 4.5,
    snapAlign: store.state.sponsors <= 4 ? "center" : "start",
  },

  1280: {
    itemsToShow: 5.5,
    snapAlign: store.state.sponsors <= 5 ? "center" : "start",
  },

  1536: {
    itemsToShow: 6.5,
    snapAlign: store.state.sponsors <= 6 ? "center" : "start",
  },
};
</script>

<style>
.carousel__viewport {
  height: 100%;
}

.carousel__track {
  height: 100%;
  justify-content: center;
}
</style>
