<template>
    <div class="flex basis-full grow-0 overflow-y-hidden">
        <div id="messages" class="flex flex-col flex-nowrap basis-full grow-0 pl-2 pr-4 gap-4 overflow-y-auto">
            <div v-for="(question, index) of pollQuestions"
                class="flex flex-col gap-2 bg-white rounded text-black p-2 border-2 border-sky-800" :key="index">

                <div class="text-lg font-medium">{{ question.question }}</div>

                <template v-if="question.done">
                    <div class="text-center text-sky-800">
                        Gracias por participar!
                    </div>
                </template>
                <template v-else>
                    <template v-if="question.type === 0">
                        <form class="flex flex-col mt-2">
                            <fieldset>
                                <div v-for="(alternative, index) in question.alternatives" :key="alternative.id"
                                    class="flex items-center mb-4">
                                    <input type="radio" name="alternative.id" v-model="answers[question.id]"
                                        :value="index"
                                        class="w-4 h-4 text-blue-600 bg-gray-300 border-gray-500 focus:ring-blue-500">
                                    <label class="ml-2 text-gray-900 ">{{ alternative.alternative }}</label>
                                </div>
                            </fieldset>
                        </form>
                    </template>

                    <template v-if="question.type === 1">
                        <div class="flex flex-col mt-2">
                            <div v-for="alternative in question.alternatives" :key="alternative.id"
                                class="flex items-center mb-4">
                                <input type="checkbox" @change="updateAnswer($event, question.id, alternative.id)"
                                    class="w-4 h-4 text-blue-600 bg-gray-300 border-gray-500 focus:ring-blue-500">
                                <label class="ml-2 text-gray-900 ">{{ alternative.alternative }}</label>
                            </div>
                        </div>
                    </template>

                    <button type="submit" class="btn uppercase" @click="sendAnswer(question)">Enviar</button>
                </template>
            </div>
        </div>
    </div>
</template>

<script setup>
import { useAuth } from '@websanova/vue-auth'
import { computed, inject, ref } from 'vue'
import { useStore } from 'vuex'
const auth = useAuth()
const store = useStore()
const axios = inject('axios')
const pollQuestions = computed(() => store.state.pollQuestions)
const answers = ref({})

const updateAnswer = (event, questionId, alternativeId) => {
    if (!answers.value[questionId]) {
        answers.value[questionId] = []
    }

    if (event.target.checked && !answers.value[questionId].includes(alternativeId)) {
        answers.value[questionId].push(alternativeId)
    } else if (!event.target.checked && answers.value[questionId].includes(alternativeId)) {
        const index = answers.value[questionId].findIndex(id => id === alternativeId)
        console.log(index)
        answers.value[questionId].splice(index, 1)
    }
}

const sendAnswer = async (poll) => {
    store.commit('pollQuestionDone', poll.id)

    switch (poll.type) {
        case 0:
            try {
                await axios.post(
                    `streaming/participations/${auth.user().id}/questions/${poll.id
                    }`,
                    {
                        alternative_id: poll.alternatives[answers.value[poll.id]].id,
                        activity_id: store.state.activity_id,
                    }
                )
            } catch (e) {
                console.error(e)
            }
            break

        case 1:
            console.log()
            try {
                await axios.post(
                    `streaming/participations/${auth.user().id}/questions/${poll.id
                    }`,
                    {
                        alternative_ids: answers.value[poll.id],
                        activity_id: store.state.activity_id,
                    }
                )
            } catch (e) {
                console.error(e)
            }
            break

        case 2:
            try {
                await axios.post(
                    `streaming/participations/${auth.user().id}/questions/${poll.id
                    }`,
                    {
                        text: answers.value[poll.id],
                        activity_id: store.state.activity_id,
                    }
                )
            } catch (e) {
                console.error(e)
            }
            break
    }
}
</script>

