import { createAuth } from "@websanova/vue-auth";
import driverAuthBearer from "@websanova/vue-auth/dist/drivers/auth/bearer.esm.js";
import driverHttpAxios from "@websanova/vue-auth/dist/drivers/http/axios.1.x.esm.js";
import driverRouterVueRouter from "@websanova/vue-auth/dist/drivers/router/vue-router.2.x.esm.js";

import axios from "axios";
import router from "../router";

/**
 * Now we will save the axios ajax information so we don't have to write it
 * all for every request
 */
axios.defaults.headers.common["Content-Type"] = "application/json";
axios.defaults.baseURL =
  window.location.protocol +
  "//" +
  window.location.hostname +
  (window.location.port ? ":" + window.location.port : "") +
  "/";
// axios.defaults.baseURL =
//   process.env.NODE_ENV !== "production"
//     ? window.location.protocol +
//       "//" +
//       window.location.hostname +
//       ":" +
//       window.location.port +
//       "/"
//     : "https://venoot-pro.work/";

const auth = createAuth({
  plugins: {
    http: axios,
    router,
  },

  drivers: {
    http: driverHttpAxios,
    auth: driverAuthBearer,
    router: driverRouterVueRouter,
  },

  options: {
    loginData: {
      url: "streaming/auth/login",
      fetchUser: true,
      rememberMe: true,
    },
    logoutData: {
      url: "streaming/auth/logout",
      method: "POST",
      makeRequest: true,
    },
    fetchData: { url: "streaming/auth/me", method: "GET", enabled: true },
    refreshData: { url: "streaming/auth/refresh", enabled: true, interval: 15 },
  },
});

export { auth, axios, router };
