<template>
  <div class="flex flex-col gap-4 basis-full grow-0 overflow-y-auto">
    <div
      v-for="(activities, date) in store.state.activities"
      class="flex flex-col gap-4"
    >
      <div class="text-center uppercase py-2 bg-black/40">
        {{ formatDate(date) }}
      </div>

      <div
        v-for="activity of activities"
        class="flex flex-row gap-2 mx-2 bg-white text-black rounded-tl-lg rounded-r-lg p-2 items-center"
      >
        <div class="flex flex-col text-center">
          <p>{{ activity.start_time }}</p>
          <p>a</p>
          <p>{{ activity.end_time }}</p>
        </div>
        <div class="w-0.5 py-2 h-full">
          <div class="w-full h-full bg-slate-600"></div>
        </div>
        <div class="flex flex-col">
          <p>{{ activity.name }}</p>
          <p class="text-sm italic text-slate-600">{{ activity.location }}</p>
          <p class="text-sm mt-1 line-clamp-3">{{ activity.description }}</p>
          <div class="flex flex-row">
            <div v-if="activity.extra_link">
              <ExternalLinkIcon
                class="h-5 w-5 text-blue-600 cursor-pointer"
                @click="goToLink(activity.extra_link)"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ExternalLinkIcon } from "@heroicons/vue/outline";
import { useStore } from "vuex";
const store = useStore();

const dateOptions = {
  weekday: "long",
  year: "numeric",
  month: "long",
  day: "numeric",
};
const formatDate = (dateString) => {
  const date = new Date(dateString + " 00:00");
  return date.toLocaleDateString(undefined, dateOptions);
};

const goToLink = (url) => {
  window.open(url);
};
</script>
