<script setup>
import { useAuth } from "@websanova/vue-auth";
import { inject, onMounted, ref } from "vue";
import { useStore } from "vuex";
import Echo from "laravel-echo";

const auth = useAuth();
const store = useStore();
const axios = inject("axios");
const uuid = ref(store.state.uuid);
const loading = ref(true);

console.log('venoot-chat v 1.1.2')

let priColor = '#1576BB'
let secColor = '#0284C7'
let triColor = '#073653'

const getSocketId = async () => {
  return new Promise((resolve) =>
    window.Echo.connector.socket.on("connect", () =>
      resolve(window.Echo.connector.socket.id)
    )
  );
};

const loadData = async () => {
  var response = await axios.post("streaming/activities", {
    activity_id: store.state.activity_id,
  });
  
  priColor = response.data.primary;
  secColor = response.data.secondary;
  triColor = response.data.terciary;
  
  store.commit("setEventName", response.data.event_name);
  store.commit("setEventLogo", response.data.logo);
  store.commit("setEventBanner", response.data.banner);
  store.commit("setActivities", response.data.activities);
  store.commit("setSponsors", response.data.sponsors);
  store.commit("setChats", {
    sharedChat: response.data.shared_chat,
    oneToOneChat: response.data.one_to_one_chat,
    speakerChat: response.data.speaker_chat,
  });

  if (store.state.activity_id) {
    store.commit("setActivity", response.data.activity);
  }

  if (auth.user().id !== response.data.speaker) {
    store.commit("setSpeaker", response.data.speaker);
  }

  response = await axios.post("streaming/video", {
    event_id: auth.user().event_id,
    activity_id: store.state.activity_id,
  });

  if (response.data.videourl) {
    store.commit("setVideoUrl", response.data.videourl.replace(/\s/g, ""));
  }  
  store.commit("setReady", true);
  return Promise.resolve();
};

const joinChannels = async () => {
  const channel = store.getters.channel(auth.user().event_id);
  window.Echo = new Echo({
    broadcaster: "socket.io",
    host: window.location.protocol + "//" + window.location.hostname + (window.location.port ? ":" + window.location.port : "") + "/",
    path: "/ws/socket.io/",
    // client: io,

    auth: {
      headers: {
        Accept: "application/json",
        Authorization: "Bearer " + auth.token(),
      },
    },
  });

  const socketId = await getSocketId();
  axios.defaults.headers.common["X-Socket-Id"] = socketId;

  window.Echo.join(channel)
    .here((users) => {
      store.commit(
        "setParticipants",
        users.filter((user) => user.id !== auth.user().id)
      );
    })
    .joining((participant) => {
      store.commit("addParticipant", participant);
    })
    .leaving((participant) => {
      store.commit("removeParticipant", participant);
    })
    .listen("StreamChatMessageEvent", (data) => {
      console.log(data.message);
      console.log(auth.user().participant.data.name);

      if (
        data.message.name ===
        `${auth.user().participant.data.name} ${
          auth.user().participant.data.lastname
        }`
      ) {
        return;
      }

      data.message.createdAt = Date.now();
      data.message.show = true;

      store.commit("addToMessageList", data.message);
    })
    .listen("PollQuestionEvent", (e) => {
      store.commit("addToPollQuestions", e.question);
    })
    .listen("QuestionRequestEvent", (e) => {
      store.commit("addToQuestionRequest", e.question_request);
    })
    .listen("ParticipantQuestionEvent", (e) => {
      store.commit("addOtherQuestionsToQuestion", e.question);
    })
    .listen("PollAnswerEvent", (e) => {
      store.commit("addAnswerToAnswers", e.summary);
    });

  // window.Echo.private(`participant-${auth.user().id}`).listen(
  //   "StreamPrivateChatMessageEvent",
  //   (data) => {
  //     data.message.createdAt = Date.now()
  //     data.message.show = true
  //     store.commit('addToPrivateMessageList', data.message)
  //   }
  // )
};

const runKeepAlive = async () => {
  await axios.post("streaming/keepAlive", {
    activity_id: store.state.activity_id,
  });

  setInterval(async () => {
    await axios.post("streaming/keepAlive", {
      activity_id: store.state.activity_id,
    });
  }, 37000);
};

const submit = async (otherUUID) => {
  await auth.login({
    data: {
      uuid: otherUUID ?? uuid.value,
    },
  });

  if (auth.check()) {
    await loadData();
    await joinChannels();
    runKeepAlive();
  }

  return;
};

onMounted(async () => {
  await auth.load();

  // // TENEMOS UUID EN LA URL
  if (uuid.value) {
    // Si estamos logeados...
    if (auth.check()) {
      // Local logout
      await auth.logout({ makeRequest: false });
    }

    // Try to login with given uuid
    await submit();

    // NO HAY UUID EN LA URL
  } else {
    // SI ESTAMOS LOGEADOS CON TOKEN PEDIMOS DATA
    if (auth.check()) {
      await loadData();
      await joinChannels();
      runKeepAlive();
    }
  }

  window.history.pushState({}, "", store.state.originalURL);
  loading.value = false;
});
</script>

<template>
  <div
    class="min-h-screen text-white"
    :style="`background: radial-gradient(circle at 40% 20%, ${priColor}, ${secColor} 20%, ${triColor} 100%);`"
  >
    <!-- AUTH READY -->
    <div v-if="auth.ready()">
      <!-- USER LOGGED -->
      <router-view v-if="auth.check()"></router-view>

      <!-- USER NOT LOGGED -->
      <div
        v-else
        class="min-h-screen bg-black/50 flex justify-center items-center"
      >
        <form class="card">
          <div class="mb-6">
            <label
              for="email"
              class="block mb-2 text-sm font-medium text-gray-300"
              >Numero Codigo QR</label
            >
            <input
              type="text"
              id="uuid"
              v-model="uuid"
              :disabled="loading"
              class="border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 disabled:cursor-not-allowed disabled:border-gray-600 disabled:bg-slate-600 block p-2.5 w-96"
              placeholder="Ejemplo: ca3b9f36-48a9-4f06-8362-bc8815c1b89b"
              required=""
            />
          </div>
          <button type="submit" class="btn">Enviar</button>
        </form>
      </div>
    </div>

    <!-- AUTH NOT READY -->
    <div
      v-else
      class="min-h-screen bg-black/50 flex justify-center items-center"
    >
      <div class="card">
        <svg
          role="status"
          class="w-8 h-8 mx-auto mb-2 text-gray-200 animate-spin"
          :style="`fill: ${priColor}`"
          viewBox="0 0 100 101"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z"
            fill="currentColor"
          />
          <path
            d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z"
            fill="currentFill"
          />
        </svg>
        Cargando
      </div>
    </div>
  </div>
</template>

