import { createStore } from 'vuex'

const originalURL = window.location.href
const queryString = window.location.search
const urlParams = new URLSearchParams(queryString)
const uuid = urlParams.get('uuid') ?? null
const mode = urlParams.get('mode') ?? "remote"
const activity_id = urlParams.get('activity') ?? null

// Create a new store instance.
const store = createStore({
  state() {
    return {
      originalURL,
      imagesURL:
        process.env.NODE_ENV === 'production'
          ? window.location.protocol +
          '//' +
          window.location.hostname +
          ':' +
          window.location.port +
          '/public/images'
          : 'https://venoot-pro.work/public/images',
      uuid,
      mode,
      activity_id,
      eventName: '',
      logo: null,
      banner: null,
      activity: null,
      videoUrl: null,
      speaker: null,
      ready: false,

      participants: [],
      activities: [],
      sponsors: [],
      messageList: [
        {type:"message", name: "Anfitrión", text: 'Bienvenidos!'},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
        // {type:"message", name: "Cristobal Diaz", text: "Mensaje de prueba!"},
      ],
      privateMessageList: [],
      questionRequests: [],
      pollQuestions: [],
      pollAnswers: [],

      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
    }
  },

  getters: {
    channel: state => eventId => {
      return state.activity_id ? 'event-' + eventId + '-activity-' + state.activity_id : 'event-' + eventId
    }
  },

  mutations: {
    setWindowWidth(state, value) {
      state.windowWidth = value
    },

    setWindowHeight(state, value) {
      state.windowHeight = value
    },

    setEventName(state, name) {
      state.eventName = name
    },

    setEventLogo(state, logo) {
      state.logo = logo
    },

    setEventBanner(state, banner) {
      state.banner = banner
    },

    setActivities(state, activities) {
      state.activities = activities
    },

    setSponsors(state, sponsors) {
      state.sponsors = sponsors
    },

    setChats(state, { sharedChat, oneToOneChat, speakerChat }) {
      state.sharedChat = sharedChat
      state.oneToOneChat = oneToOneChat
      state.speakerChat = speakerChat
    },

    setActivity(state, activity) {
      state.activity = activity
    },

    setSpeaker(state, id) {
      if (!state.speaker) state.speaker = id

      if (
        state.participants.findIndex(participant => participant.id === id && participant.name !== 'Speaker') >=
        0
      ) {
        state.participants.unshift({
          id,
          name: 'Speaker',
        })
      }
    },

    setVideoUrl(state, url) {
      state.videoUrl = url
    },

    setParticipants(state, participants) {
      state.participants = participants
      if (
        state.participants.findIndex(
          participant => participant.id === state.speaker && participant.name !== 'Speaker'
        ) >= 0
      ) {
        state.participants.unshift({
          id: state.speaker,
          name: 'Speaker',
        })
      }
    },

    addParticipant(state, participant) {
      state.participants.push(participant)
      if (
        state.participants.findIndex(
          participant => participant.id === state.speaker && participant.name !== 'Speaker'
        ) >= 0
      ) {
        state.participants.unshift({
          id: state.speaker,
          name: 'Speaker',
        })
      }
    },

    removeParticipant(state, participant) {
      state.participants = state.participants.filter(function (obj) {
        return obj.id !== participant.id
      })
    },

    addToMessageList(state, message) {
      state.messageList.push(message)

      if (state.messageList.length > 50) {
        state.messageList.shift()
      }
    },

    addToPollQuestions(state, question) {
      const index = state.pollQuestions.findIndex(otherQuestion => otherQuestion.id === question.id)

      if (index < 0) {
        state.pollQuestions.push(question)
        state.messageList.push({ type: 'poll' })
      }
    },

    addToQuestionRequest(state, question) {
      state.questionRequests.push(question)
      state.messageList.push({ type: 'question' })
    },

    addOtherQuestionsToQuestion(state, otherQuestion) {
      const index = state.questionRequests.findIndex(q => {
        return q.id === otherQuestion.question_request.id
      })

      let question = state.questionRequests[index]

      if (question) {
        if (!('otherQuestions' in question)) {
          question.otherQuestions = []
        }
        question.otherQuestions.push(otherQuestion)
      }

      state.questionRequests.splice(index, 1, question)
    },

    addAnswerToAnswers(state, answer) {
      const index = state.pollAnswers.findIndex(otherAnswer => otherAnswer.id === answer.id)
      let summary = {
        id: answer.id,
        question: answer.question,
        optionsReferer: {
          chart: {
            foreColor: '#121212',
            offsetX: -30,
          },
          legend: {
            show: false,
          },
          labels: [],
        },

        seriesReferer: [],
      }

      for (let alternative of answer.alternatives) {
        summary.optionsReferer.labels.push(alternative.alternative)
        summary.seriesReferer.push(answer.answers[alternative.id] ? answer.answers[alternative.id] : 0)
      }

      if (index >= 0) {
        state.pollAnswers.splice(index, 1, summary)
      } else {
        state.pollAnswers.push(summary)
      }
    },

    addToPrivateMessageList(state, message) {
      state.privateMessageList.push(message)

      if (state.privateMessageList.length > 50) {
        state.privateMessageList.shift()
      }
    },

    questionDone(state, questionId) {
      const index = state.questionRequests.findIndex(qrs => qrs.id === questionId)
      let qr = state.questionRequests[index]
      qr.done = true
      // state.questionRequests.splice(index, 1, qr)
    },

    pollQuestionDone(state, questionId) {
      const index = state.pollQuestions.findIndex(pqs => pqs.id === questionId)
      let pq = state.pollQuestions[index]
      pq.done = true
      // state.pollQuestions.splice(index, 1, qr)
    },

    setReady(state, value) {
      state.ready = value
    },
  }
})

window.addEventListener('resize', () => {
  store.commit('setWindowWidth', window.innerWidth)
  // store.commit('setWindowHeight', window.innerHeight)
})

export default store